/* 
This document contains the general styling for the Making Matters book.
Styles can be overwritten from the CSS view in the wiki.

https://pagedjs.org/documentation/
https://jamonserrano.github.io/plumber-sass/
*/
/* Paged and print specific styles are collected in paged.css */
/* Pagedjs/paged media specific styles */
@page {
  bleed: 4mm;
  marks: crop;
  size: 110mm 200mm;
  margin-top: 15mm;
  margin-right: 10mm;
  margin-bottom: 25mm;
  margin-left: 15mm;
  /* bleed: 6mm;
   marks: crop cross; */
  background-color: white;
  @footnote {
    float: bottom;
  }
  @bottom-right-corner {
    content: counter(page);
  }
  @top-left {
    content: string(articleTitle);
  }
  @top-right {
    content: string(chapterTitle);
  }
}
@page :left {
  @bottom-right-corner {
    content: "";
  }
  @bottom-left-corner {
    content: counter(page);
  }
}
@page layout-title {}
@page layout1 {
  margin-right: 40mm;
}
@page layout2 {}
.title-image-wrap {
  page: layout-title;
}

.layout-1 {
  page: layout1;
}

.layout-2 {
  page: layout2;
}

/*
	because we're changing the right margin based on layout
	we need to fix the top-right margin box
	Ugly, but best i could come up with
*/
.pagedjs_pagebox .pagedjs_margin-top-right {
  position: absolute;
  right: var(--pagedjs-margin-left);
  text-align: right;
  top: 0;
  height: var(--pagedjs-margin-top);
}

.pagedjs_sheet {
  z-index: 1;
}

/* Setting variables */
h2,
.chapter-title {
  string-set: chapterTitle content(text);
}

h3 {
  string-set: articleTitle content(text);
}

h2 {
  counter-increment: countChapter;
  counter-set: footnote-marker 0 footnote 0;
  string-set: author "";
}

span.author {
  string-set: author content(text);
}

/* PAGE NUMBERS */
.pagedjs_pagebox .pagedjs_margin-bottom-left-corner-holder,
.pagedjs_pagebox .pagedjs_margin-bottom-right-corner-holder {
  position: relative;
}
.pagedjs_pagebox .pagedjs_margin-bottom-left-corner-holder .pagedjs_margin-content,
.pagedjs_pagebox .pagedjs_margin-bottom-right-corner-holder .pagedjs_margin-content {
  position: absolute;
  bottom: 0;
  width: 100pt !important;
  font-size: 44pt;
  padding: 0 10pt;
  line-height: 1.4;
}
.pagedjs_pagebox .pagedjs_margin-bottom-left-corner-holder .pagedjs_margin-content {
  left: 0;
  text-align: left;
}
.pagedjs_pagebox .pagedjs_margin-bottom-right-corner-holder .pagedjs_margin-content {
  right: 0;
  text-align: right;
}

