/* Pagedjs/paged media specific styles */

@page {
	bleed: 4mm;
	marks: crop;
  size: 110mm 200mm;
  margin-top: 15mm;
  margin-right: 10mm;
  margin-bottom: 25mm;
  margin-left: 15mm;
  /* bleed: 6mm;
   marks: crop cross; */
	background-color: white;
  @footnote {
    float: bottom;
  }
	@bottom-right-corner {
    content: counter(page);
  }
	@top-left { 
		content: string(articleTitle);
  }
	@top-right {
		content: string(chapterTitle);
	}
}

@page:left {
	@bottom-right-corner {
    content: '';
  }
  @bottom-left-corner {
		content: counter(page);
  }
}


