/* PAGE NUMBERS */
.pagedjs_pagebox {

	.pagedjs_margin-bottom-left-corner-holder,
	.pagedjs_margin-bottom-right-corner-holder {
		position: relative;
	
		.pagedjs_margin-content {
			position: absolute;
			bottom: 0;
			width: 100pt !important;
			// @include font-louise-regular(); // moved to typography
			font-size: 44pt;
			padding: 0 10pt;
			line-height: 1.4; // any lower and descenders go off the page
		}
	}

	.pagedjs_margin-bottom-left-corner-holder {
		.pagedjs_margin-content {
			left: 0;
			text-align: left;
		}
	}

	.pagedjs_margin-bottom-right-corner-holder {
		.pagedjs_margin-content {
			right: 0;
			text-align: right;
		}
	}
}




